import React from 'react';
import { motion } from 'framer-motion';
import Button from '../components/Button';
import { useTheme } from '../contexts/ThemeContext';
import { colors, typography } from '../styles/sharedStyles';

function About() {
  const { darkMode } = useTheme();

  return (
    <div className={`${colors.background} ${typography.sans} ${darkMode ? 'dark' : ''} min-h-screen`}>
      <section className={`${colors.primary} text-white py-16`}>
        <div className="container mx-auto px-4">
          <motion.h1 
            className="text-4xl font-bold mb-4 text-center"
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            Conozca a Roger Salas Granados
          </motion.h1>
          <motion.p 
            className="text-xl text-center"
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            Un líder visionario en el campo legal costarricense
          </motion.p>
        </div>
      </section>

      <section className="py-16">
        <div className="container mx-auto px-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center mb-16">
            <motion.div
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
            >
              <img src="/api/placeholder/400/400" alt="Roger Salas Granados" className="rounded-full mx-auto w-64 h-64 object-cover shadow-lg" />
            </motion.div>
            <motion.div
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              <h2 className="text-3xl font-bold mb-4 dark:text-white">Trayectoria Profesional</h2>
              <p className={`${colors.text} dark:text-gray-300 mb-4`}>
                Con más de 15 años de experiencia, Roger Salas Granados se ha consolidado como un referente en derecho corporativo y tecnológico en Costa Rica. Su enfoque innovador ha sido clave en el éxito de numerosas transacciones internacionales y casos de alta complejidad.
              </p>
              <p className={`${colors.text} dark:text-gray-300 mb-4`}>
                Graduado con honores de la Universidad de Costa Rica en 2005, Roger complementó su formación con una Maestría en Derecho Internacional y Tecnológico de la Universidad de Cambridge en 2007, donde se especializó en regulación de nuevas tecnologías.
              </p>
            </motion.div>
          </div>

          <motion.div 
            className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-8 mb-16"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.4 }}
          >
            <h2 className="text-2xl font-semibold mb-4 dark:text-white">Filosofía y Enfoque</h2>
            <ul className="list-disc list-inside space-y-2 dark:text-gray-300">
              <li>Compromiso con la innovación legal y la adopción de tecnologías</li>
              <li>Enfoque multidisciplinario en derecho, negocios y tecnología</li>
              <li>Dedicación a la formación continua y participación en foros internacionales</li>
              <li>Mentoría a jóvenes abogados y compromiso con la diversidad</li>
              <li>Promoción de la resolución alternativa de conflictos</li>
            </ul>
          </motion.div>

          <motion.div 
            className={`${colors.secondary} text-white p-8 rounded-lg mb-16`}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.6 }}
          >
            <h2 className="text-2xl font-semibold mb-4">Logros y Reconocimientos</h2>
            <ul className="list-disc list-inside space-y-2">
              <li>"Abogado del Año" en Derecho Tecnológico (2022)</li>
              <li>Liderazgo en la mayor transacción tecnológica de Centroamérica (2021)</li>
              <li>Autor de "Derecho y Tecnología: Navegando el Futuro Legal" (2020)</li>
              <li>Ponente regular en conferencias internacionales</li>
              <li>Miembro del comité asesor para la reforma de la Ley de Protección de Datos</li>
            </ul>
          </motion.div>
        </div>
      </section>

      <section className="py-16 bg-gray-100 dark:bg-gray-900">
        <div className="container mx-auto px-4 text-center">
          <h2 className="text-3xl font-bold mb-8 dark:text-white">Compromiso con la Comunidad</h2>
          <p className="text-xl mb-8 dark:text-gray-300">
            Roger lidera un programa de mentoría para estudiantes de derecho de comunidades subrepresentadas, buscando fomentar una mayor diversidad en el campo legal costarricense.
          </p>
          <Button to="/contacto" variant="primary">Agendar Consulta con Roger</Button>
        </div>
      </section>
    </div>
  );
}

export default About;